* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  gap: 10px;
  display: flex;
}

.field {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.timer .value {
  font-size: 30px;
  font-weight: 500;
}

.form {
  flex-direction: row;
  align-items: center;
  gap: 30px;
  display: flex;
}

.form input {
  max-width: 100px;
}

.form button {
  color: #fff;
  background-color: #000;
  padding: 10px;
}

